import { Box } from "@material-ui/core";
import React, { useCallback, useState, useEffect } from "react";
import { connect } from "react-redux";
import { Link, useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import { AnyAction } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { Loader } from "../ui/Loader";
import { SingleLineInput } from "../ui/SingleLineInput";
import { useUrlQuery } from "../../core/hooks";
import { __tr } from "../../i18n";
import { Routes } from "../../routes";
import { IAppState } from "../../state";
import { initializeState } from "../../state/middlewares";
import styles from "../../styles/Login.module.scss";
import axios from "axios";

const mapState = (state: IAppState) => {
  return {
    user: state.userState.user,
  };
};

const mapDispatch = (dispatch: ThunkDispatch<IAppState, {}, AnyAction>) => {
  return {
    initialize: () => dispatch(initializeState()),
  };
};

type Props = ReturnType<typeof mapState> & ReturnType<typeof mapDispatch>;

function Login(props: Props) {
  const [loading, setLoading] = useState(false);
  const [email, setEmail] = useState<string | undefined>();
  const [phone, setPhone] = useState<string | undefined>();
  const [errors, setErrors] = useState<any>({});

  const history = useHistory();
  const nextSegment = useUrlQuery("next", "");
  const isCreatePassword = useUrlQuery("new", "");
  const urlParamsEmail = useUrlQuery("email", "");
  const urlParamsPhone = useUrlQuery("phone", "");

  //setting user email/phone in state -manish
  useEffect(() => {
    if (urlParamsEmail) {
      setEmail(urlParamsEmail);
    } else if (urlParamsPhone) {
      setPhone(`+${urlParamsPhone.substring(1, urlParamsPhone.length)}`);
    }
  }, [urlParamsEmail, urlParamsPhone]);
  const sharecampaignaccess = useCallback(() => {
    const urlSearchParams = new URLSearchParams(window.location.search);
    const params = Object.fromEntries(urlSearchParams.entries());
    // alert(params.share);
    if (params.share) {
      alert("hit");
      axios
        .post(
          "/api/campaign/campaignaccessstatus",
          {
            campaignId: params.campaignId,
            userId: params.userId,
            status: "active",
          },
          { headers: { Authorization: res.data.token } }
        )
        .then((data) => {
          alert(data.data);
          // console.log("Data");
        })
        .catch((err) => {
          alert(err);
          console.log(err);
        });
    }
  })
  const onSubmit = useCallback(
    async (ev: React.ChangeEvent<HTMLFormElement>) => {
      ev.preventDefault();

      let form = ev.currentTarget;
      let data = {
        email,
        phone,
        password: form?.password?.value?.trim(),
      };

      let errs: any = {};
      if (!data.password || data.password.length < 8) {
        errs.password = __tr("errorInvalidPassword");
      }

      setErrors(errs);

      if (Object.keys(errs).length > 0) {
        return;
      }

      setLoading(true);
      axios
        .post("/api/auth/login", data)
        .then((res) => {
          setLoading(false);
          props.initialize();

          localStorage.setItem("token", res.data.token);
          const urlSearchParams = new URLSearchParams(window.location.search);
          const params = Object.fromEntries(urlSearchParams.entries());
          // alert(params.share);
          if (params.share) {
            alert("hit");
            axios
              .post(
                "/api/campaign/campaignaccessstatus",
                {
                  campaignId: params.campaignId,
                  userId: params.userId,
                  status: "active",
                },
                { headers: { Authorization: res.data.token } }
              )
              .then((data) => {
                alert(data.data);
                // console.log("Data");
              })
              .catch((err) => {
                alert(err);
                console.log(err);
              });
          }
          return history.replace(Routes.Home);
        })
        .catch((err) => {
          setLoading(false);
          toast.error(err?.response?.data);
        });
    },
    [email, phone, history, nextSegment, props]
  );

  useEffect(() => {
    if (props.user) {
      sharecampaignaccess()
      history.replace(Routes.BaseCampaigns);
    }
  }, [history, props]);

  return (
    <div className={styles.loginWrapper}>
      <h2>Welcome back !</h2>
      <div className={styles.formWrapper}>
        <h3>Password</h3>
        <form action="" className={styles.form} onSubmit={onSubmit}>
          {loading && <Loader />}
          <Box marginY={1} marginTop={4}>
            <SingleLineInput
              label={`${isCreatePassword ? "Create" : "Enter"} Password*`}
              type="password"
              name="password"
              placeholder="Must be at least 8 characters"
              error={errors.password}
            />
          </Box>
          <Box marginY={0}>
            <Link
              to={Routes.ForgotPassword}
              className={styles.passwordRecoveryLink}
            >
              Forgot Password ?
            </Link>
          </Box>
          <Box marginY={2} marginTop={2}>
            <button type="submit" className={styles.btnSubmit}>
              Sign In
            </button>
          </Box>
          <div className={styles.btnBack}>
            <Link to={Routes.Login} className={`${styles.signupLink}`}>
              {__tr("back")}
            </Link>
          </div>
        </form>
      </div>
    </div>
  );
}

export default connect(mapState, mapDispatch)(Login);
