interface Props {
    id: string | number;
    profilePic: string;
    userName: string;
    message: string;
    time?: string;
    isOnline?: boolean;
    numUnreadMessages?: number;
    selected?: boolean;
    status?:string;
    onClick?: () => void;
    searched?: boolean;
    deletedaccessdata?:string;
    email?:string;
    Remove?:string;
    userId?:string;
}

interface Props {
    user?: any;
  }

export const UserCard: React.FC<Props> = ({
    profilePic,
    id,
    userName,
    onClick,
    status,
    email,
    searched,
    Remove,
    deletedaccessdata
}) => {
    return (
        <>
            <li
                key={id}
                onClick={onClick}
            >
                <Grid container spacing={2} >
                    <Grid item xs={2}>
                        <div className={style.image} style={{ height: '30px', width: "30px" }}>
                            <img
                                src={profilePic}
                                alt={userName}
                                className={style.image}
                                // alt='profile'
                                id="userProfile"
                            />
                        </div>
                    </Grid>
                    <Grid item xs={6}>
                       <span >{email}</span>
                    </Grid>
                  
                    <Grid item xs={2}>
                    <span >{status}</span>
                    </Grid>
                    <Grid item xs={2} style={{marginTop:"-5px"}}>
                       <Button onClick={deletedaccessdata}>{Remove}</Button>
                    </Grid>
                  
                </Grid>
            </li>

        </>
    );
}

export function ShareCampaignAccess(props: Props) {
    const Accessdata={
        userId:"",
        email:"",
        status: "pending",
        imageUrl: "",
    };
    const [open, setOpen] = React.useState(false);
    const [searchedusers, setsearchedusers] = React.useState<any[]>([]);
    const [searchedtext, setsearchedtext] = React.useState('');
    const [accessedUsers, setaccessedUses] = React.useState<any[]>([]);
    const { user } = useAppUser();
    const [id, setId] = React.useState();
    const token = localStorage.getItem("token");
    const [profilepic,setProfilePic]= React.useState();
    const [accesData,setAccessData]= React.useState<AccesedUserData[]>([]);
    const [errors, setErrors] =React.useState<any>({});
    const dispatch = useDispatch();
    let [AcceseduserList, setAcceseduserList] = React.useState<any[]>([])
    let [mydata,setdata] =React.useState<any[]>([]);
    const [loading, setLoading] = React.useState(false);
    useEffect(()=>{
        if (accesData.length === 0) {
            setAccessData([Accessdata]);
          }
    },[])

    const validation=useCallback(()=>{
        let nextErr:any={};
        if (!Validators.isEmail(searchedtext || "")) {
          nextErr.searchedtext = "please enter proper and valid email";
        }
        setErrors(nextErr);
         return nextErr;
      },[searchedtext])

    const selectchat = (e:any) => {
        // console.log('eeeeeeeeeeeeee',e)
        setProfilePic(e.profilePic)
        setsearchedtext(e.email)
        mydata.push({
            userId:e.userId,
            email:e.email,
            status: "pending",
            imageUrl:e.profilePic
           })
           setdata( mydata)
           setsearchedusers([])
        
    }
    const nonExistUser=(value:string)=>{
        const Accessdata: typeof accesData =[];
        accesData.forEach((m) => {
           let userdata={
            userId:m.userId,
            email:value,
            status: "pending",
            imageUrl: "https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png"
           }
           Accessdata.push(userdata)
         });
        setAccessData(Accessdata)
       };
    
       console.log("nonnn",accesData)
    const  addUser = useCallback(async () => { 
        let newErrors=validation();
    console.log("er",newErrors)
    if (Object.keys(newErrors).length> 0) {
      Object.keys(newErrors).forEach((er) => {
        //  toast.warn(newErrors[er]);
        console.log(newErrors)
      });
      return;
    }

        if(mydata.length===0){
           mydata=accesData
          console.log("hii",mydata)
        }
        console.log("e",mydata[0].email)
        let dataIndex = AcceseduserList.findIndex((c:any) => {
            return c.email === mydata[0].email;
        })
        if( dataIndex===-1){
        let userAccessData = {
            campaignId: props.user,
            accessData:mydata
        };  
         console.log("my", userAccessData)
        try {
           axios
            .post("/api/campaign/sharecampaignaccess",userAccessData, { headers: { Authorization: token } })
            .then((res) => {
                let dataIndex = AcceseduserList.findIndex((c:any) => {
                    return c?.email === mydata[0].email;
                 })
                 if(dataIndex === -1) {
                    AcceseduserList = [mydata[0], ...AcceseduserList];
                 }
                else {
                    toast.warn("campaign is already shared with this user");
                }
                // console.log("index", mydata)
                // onClose();
              // console.log("next",AcceseduserList)
            setAcceseduserList(AcceseduserList)
            });
            setsearchedtext("");
            setId(" ")
            setdata([])
            setAccessData([Accessdata])
            toast.success("successfullyshared");
         
        } 
        catch (error) {
            toast.warn((error as any).message);
        }
    }
    else{
        toast.warn("campaign is already shared with this user");
    }
      }, [ dispatch,token,accesData,mydata,validation]);
    
    
      const updateStatus = (e: any,i:any) => {
       const updateCampaginStatus = {
        campaignId:props.user,
        userId:e?.userId || undefined,
        email: e.email,
        status:"deleted"
    };
    console.log("data",updateCampaginStatus)
        if (token) {
          axios
            .post("/api/campaign/campaignaccessstatus", updateCampaginStatus,{headers: { Authorization: token },})
            .then((res) => {
               console.log("delete",res.data);
              AcceseduserList = AcceseduserList.filter((b) => {
                return b.email !== e.email;
            })
            setAcceseduserList(AcceseduserList);
             setLoading(true);
            // window.location.reload();
            })
            .catch((err) => {
              console.log("err", err);
            });
        }
      }; 


    const handleClickOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };
    useEffect(() => {
      if (token) {
        axios.get(`/api/campaign/id/${props.user}`, { headers: { Authorization: token } }).then(result => {
          setAcceseduserList(result.data[0].shared)
          })
    }
    }, [token])    
    const searchUser = (searchuser: string) => {
      if (searchuser !== '' && searchuser.length > 2) {
            const token = localStorage.getItem('token') ?? '';
            axios.get('/api/user/search/' + searchuser, { headers: { 'Authorization': token } }).then(res => {
                const searchedUser: any[] = [];
                res.data.forEach((d: any) => {
                    const idx = accessedUsers.findIndex(c => c.chatWith[0] === d._id);
                    if (idx < 0 && d._id !== user?._id) {
                        searchedUser.push({
                            profilePic: d.imgUrl ? d.imgUrl : 'https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png',
                            email: d.email,
                            username:d.name,
                            userId: d?._id
                        })
                    }

                })
                setsearchedusers(searchedUser);
            })
        }
        else {
            setsearchedusers([]);
        }
    }

    const changeHandler=(text: string)=>{ 
         setsearchedtext(text);
         nonExistUser(text)
         searchUser(text)
    }

    // const userImg = user?.imgUrl ? user.imgUrl : 'https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png';
    return (
        <div>
            <Typography onClick={handleClickOpen}>
                Share Campaign Access
            </Typography>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    <IconButton style={{ background: "blue", marginRight: "12px",cursor:"none" }}>
                        < PersonAddIcon style={{ color: "white" }} />
                    </IconButton>
                    {"Share access with people"}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        <Box >
                        <span style={{ color: "#f44336", fontSize: "0.75rem",marginTop:'-10px'}}>{errors.searchedtext}</span>
                           <InputBase
                                style={{ width: '28vw', marginBottom: "22px", marginRight: "11px" }}
                                placeholder={"Search user"}
                                className={styles.searchInput}
                                value={searchedtext}
                                onChange={(e)=>changeHandler(e.target.value)}
                            />

                            <Button variant="contained" color="primary" onClick={addUser} disabled={!searchedtext} >Add</Button>
                           </Box>
                        {(searchedtext && searchedusers?.length > 0 && searchedusers) ?
                            <List  style={{cursor:'pointer'}}>
                                {searchedusers.map((e) => (
                                    <UserCard
                                        id={nanoid()}
                                        profilePic={e.profilePic}
                                        userName={e.username}
                                        email={e.email}
                                        searched 
                                        onClick={()=> {selectchat(e)}}
                                       />
                                ))}
                            </List> :<List>
                               {AcceseduserList?. map((e:any)=>{
                                    return(
                                        <>
                                    {e?.status!=="deleted"?
                                     <UserCard
                                    //  id={e.userId}
                                    //  userName={e.name}
                                     email={e?.email}
                                     status={e?.status}
                                     profilePic={e.imageUrl}
                                     deletedaccessdata={()=>{updateStatus(e,"")}}
                                     Remove="X"
                                     />
                                    :("")}
                                    </>
                                    );
                                })}
                               
                               </List>
                            }
                    
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="primary" autoFocus>
                        Done
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}